const Detail = ({ shoes, stock, setStock }) => {
console.log("🚀 ~ file: detail.js ~ line 17 ~ Detail ~ stock", stock)
console.log("🚀 ~ file: detail.js ~ line 17 ~ Detail ~ shoes", shoes)


  const [showDetailPage, setShowDetailPage] = useState(true);

  useEffect(()=>{
    let timer = setTimeout( () =>{
      setShowDetailPage(!showDetailPage);
    },2000)
  },[])

  let { id2 } = useParams();
  let history = useHistory();

  const maped =  shoes.map(el => {
    return el.id
  })
  // id [2, 1, 0]
  //idx [0, 1, 2]
  const realId = maped.indexOf(Number(id2))


  return (
  <div>
    <div className="container">
      {/* <박스>
        <제목 색상="blue">Detail</제목>
      </박스> */}
      {
         (showDetailPage ? 
          <div className="my-alert2">
            <p>재고가 얼마 남지 않았습니다</p>
          </div>
          : null)
      }
      <div className="row">
        <div className="col-md-6">
          <img src={"https://codingapple1.github.io/shop/shoes"  + (shoes[realId].id +1) + ".jpg"} width="100%" alt='1' />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{shoes[realId].title}</h4>
          <p>{shoes[realId].content}</p>
          <p>{shoes[realId].price}</p>
          <Info stock={stock} setStock={setStock}></Info>
          <button className="btn btn-danger" onClick=
          {
            () => {
              let newStock = [...stock];
              newStock[0] = newStock[0] - 1
              setStock(newStock);  
            }
          }
          >주문하기</button> 
          <button className="btn btn-danger" onClick={() => {
            history.goBack();
          }}>뒤로가기</button> 
        </div>
      </div>
    </div> 
  </div>
  );
};

function Info ({ stock }){
  return(
    <p>재고 : {stock[0]}</p>
  )
}

export default Detail;